* ul,
* li {
    list-style: none;
}

* button {
    border: none;
    padding: 2px;
}

header {
    position: fixed;

    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;

    width: 100%;
    height: 80px;

    * {
        padding: 0;
        margin: 0;
    }

    &>div:nth-child(1) {
        min-height: 80px;
        width: 100%;

        align-content: center;




        ul {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;

            justify-content: space-between;

            overflow-x: hidden;
        }

        z-index: 20;
    }

    &>div:nth-child(2) {
        height: auto;
        width: 100%;
        min-height: 60px;

        backdrop-filter: blur(4px);

        z-index: 10;
        opacity: .94;
    }
}

.AsideNav {
    width: 100%;

    &>nav {

        &>div:first-child {
            /*Donde serÃ¡ mostrado*/

            z-index: 12;
        }

        ul {
            overflow-x: scroll;

            height: auto;
            min-height: 140px;

            z-index: 8;

            overflow-x: scroll;
            overflow-y: hidden;

            &>li {
                display: grid;
                grid-template-rows: 100px 40px;

                margin: 5px 10px;

                height: auto;
                min-width: 120px;



                &>div:first-child {
                    display: flex;

                    justify-content: center;
                    align-items: flex-end;

                    width: 150px;

                    button {
                        position: relative;
                        width: 100%;
                        height: 100%;

                        &>img {
                            position: absolute;
                            top: 0;
                            left: 0;

                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            z-index: -1;
                        }
                    }
                }
            }

        }
    }
}

.GenLi {
    box-shadow: 0 2px 4px 2px var(--CorNav_Shadow);
}

.FocusLi {
    position: relative;
    z-index: 0;

    &::before {
        content: "";
        position: absolute;

        width: 100%;
        height: 100%;

        top: 0;
        left: 0;

        background:
            radial-gradient(circle, rgba(135, 234, 255, 1) 0%, rgba(255, 205, 0, 1) 18%, rgba(10, 188, 193, 1) 36%, rgba(0, 43, 196, 1) 52%, rgba(204, 87, 8, 1) 72%, rgba(1, 95, 96, 1) 88%, rgba(135, 234, 255, 1) 100%);

        background-size: 800%;
        border-radius: 10px;
        filter: blur(8px);

        animation: AnimButtons 20s linear infinite alternate;

        z-index: -1;
    }
}

.AsideNav {
    button.onoffbtn {
        font-size: 2rem;
        transition: letter-spacing 2s;
        border-radius: 6px;
        margin: 4px;

        &>abbr {
            transition: letter-spacing 1s,
                transform 2s;
        }

        &.enable {
            letter-spacing: 12px;
            padding: 5px;

            &>abbr {
                letter-spacing: 0px;
                transform: scaleX(.2);
            }
        }

        &.disable {
            letter-spacing: 4px;
            transition: letter-spacing 1s;

            &>abbr {
                letter-spacing: 8px;
                transform: scaleX(1);
            }
        }
    }
}

@keyframes AnimButtons {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 400% 0;
    }

    100% {
        background-position: 0 0;
    }
}