:root {
    --Header-Dark-CorText: rgb(201, 129, 216);

    --Header-Dark-Cor: #985803;
    --Header-Dark-Cor_Shadow: #452803;

    --Header-Dark-CorNav: rgba(94, 12, 110, 1);
    --Header-Dark-CorNav_Shadow: #360340;

    --Header-Dark-CorUl: rgba(143, 33, 0, 0.8);
    --Header-Dark-CorDefault: #b40000;

    --Header-Light-CorText: rgb(201, 129, 216);

    --Header-Light-Cor: #985803;
    --Header-Light-Cor_Shadow: #452803;

    --Header-Light-CorNav: rgba(94, 12, 110, 1);
    --Header-Light-CorNav_Shadow: #360340;

    --Header-Light-CorUl: rgba(143, 33, 0, 0.8);
    --Header-Light-CorDefault: #b40000;
}


.App.Dark {
    header {
        &>div:nth-child(1) {
            background-color: var(--Header-Dark-Cor);
            box-shadow: 4px 2px 2px var(--Header-Dark-Cor_Shadow);
        }
    
        &>div:nth-child(2) {
            background-color: var(--Header-Dark-CorNav);
            box-shadow: 4px 2px 2px var(--Header-Dark-CorNav_Shadow);
        }
    
        nav {
            &>div:first-child>button {
                background-color: var(--Header-Dark-CorNav_Shadow);
            }
            ul {
                background-color: var(--Header-Dark-CorUl);
    
                button {
                    background: var(--Header-Dark-CorDefault);
                    color: var(--Header-Dark-CorText);
                }
            }
        }
    }
}

.App.Light {
    header {
        &>div:nth-child(1) {
            background-color: var(--Header-Light-Cor);
            box-shadow: 4px 2px 2px var(--Header-Light-Cor_Shadow);
        }
    
        &>div:nth-child(2) {
            background-color: var(--Header-Light-CorNav);
            box-shadow: 4px 2px 2px var(--Header-Light-CorNav_Shadow);
        }
    
        nav {
            &>div:first-child>button {
                background-color: var(--Header-Dark-CorNav_Shadow);
            }
            ul {
                background-color: var(--Header-Light-CorUl);
    
                button {
                    background: var(--Header-Light-CorDefault);
                    color: var(--Header-Light-CorText);
                }
            }
        }
    }
}

html {
    scrollbar-color: rgb(146, 0, 0) rgba(113, 40, 0, 0.6);
}

.App.Dulce {
    header{
        ul {
            scrollbar-color: rgb(77, 47, 1) rgba(199, 113, 1, 0.42);
        }
    }
}